import React, { FormHTMLAttributes } from 'react';
import { Button, Form } from 'react-bootstrap';
import classNames from 'classnames/bind';
import styles from './styles.module.scss';

const cx = classNames.bind(styles);

interface IFormContainerProps extends React.HTMLProps<HTMLFormElement>{
  children: React.ReactNode,
  title: string,
  className?: string,
  onDelete?: React.MouseEventHandler,
  isCreateTable?: boolean,
  isLoading?: boolean,
}

const InputsContainer = ({ children, title, className, onSubmit, onDelete, isCreateTable, isLoading }: IFormContainerProps): React.ReactElement => (
  <div
    className={cx('form-container', className)}
  >
    <div>
      <h3>
        {title}
      </h3>
      <div className={styles['form']}>
        {children}
      </div>
    </div>
    <div className={styles['form-btn-line']}>
      <div>
        <Button variant="primary" size="sm" type="submit" disabled={isLoading}>
          {isCreateTable && !isLoading ? 'Создать' : 'Сохранить'}
          {isCreateTable && isLoading ? 'Создаем...' : 'Сохраняем...'}
        </Button>
        <Button variant="light" size="sm">
          Отменить
        </Button>
      </div>
      {!isCreateTable && (
        <Button variant="danger" size="sm" disabled={isLoading}>
          Удалить
        </Button>
      )}
    </div>
  </div>
);

export default InputsContainer;
